import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import TwoCols from "../components/layout/twoCols"
import SEO from "../components/seo"
import Slices from "../components/slices"
import GuideNav from "../components/guideNav"

import Title from "../components/layout/title"
import BreadCrumbs from "../components/layout/breadCrumbs"

export default function ProdDevTemplate({ pageContext: prodDev }) {
  return (
    <Layout>
      <SEO title={`Product Development  - ${prodDev.Title}`} />

      <div>
        <BreadCrumbs>
          {[
            <Link to={`/projects`}>‚Üê All projects</Link>,
            <Link to={`/projects/${prodDev.project.UID}`}>
              {prodDev.project.Title}
            </Link>,
            prodDev.Title,
          ]}
        </BreadCrumbs>

        <Title>Product Development: {prodDev.Title}</Title>

        <TwoCols>
          <div>
            {prodDev.Description && (
              <div>
                <h3>Description</h3>
                <div
                  dangerouslySetInnerHTML={{ __html: prodDev.Description }}
                />
              </div>
            )}

            <div>
              <h3>Competences</h3>
              <ul>
                {prodDev.Competences.map((item, key) => (
                  <li key={key}>{item.Competence}</li>
                ))}
                <li>Coming up</li>
                <li>Coming up</li>
              </ul>
            </div>

            <Slices content={prodDev.Content} />
          </div>

          <GuideNav project={prodDev.project} currentUID={prodDev.UID} />
        </TwoCols>
      </div>
    </Layout>
  )
}
