import React from "react"

export default function Slices({ content }) {
  return (
    <div>
      {content.map((item, key) => {
        switch (item.__typename) {
          case "Strapi_ComponentPageContentComponentsText2":
            return <Text key={key} content={item} />
          case "Strapi_ComponentPageContentComponentsFiles":
            return <Files key={key} content={item} />
          case "Strapi_ComponentPageContentComponentsImage":
            return <Image key={key} content={item} />
          case "Strapi_ComponentPageContentComponentsVideo":
            return <Video key={key} content={item} />
          default:
            return ""
        }
      })}
    </div>
  )
}

const Text = function ({ content }) {
  return <div dangerouslySetInnerHTML={{ __html: content.Text }}></div>
}

const Files = function ({ content }) {
  return (
    <div>
      {content.Files.map((file, key) => (
        <span key={key}>
          Download file:{" "}
          <a href={file.url} target="_blank" rel="noreferrer">
            {file.caption || file.name}
          </a>
        </span>
      ))}
    </div>
  )
}

const Image = function () {
  return <div>Image here</div>
}

const Video = function () {
  return <div>Video here</div>
}
